import React from 'react';
import PropTypes from 'prop-types';
import styles from './FriendsList.module.css';

const FriendsList = ({arr}) => {

  return(
    <ul className={styles.friens}> {
      arr.map(friend => (
<li key={friend.id} className={styles.friendItem}>
      <span className={friend.isOnline ? styles.active : styles.noactive}>{friend.isOnline}</span>
  <img className={styles.avatar} src={friend.avatar} alt="" width="48" />
      <p className={styles.name}>{friend.name}</p>
</li>
      ))
      }

</ul>
  )
};

FriendsList.propTypes = {
  arr: PropTypes.arrayOf(PropTypes.shape({
    avatar: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    isOnline: PropTypes.bool.isRequired,
    id: PropTypes.number.isRequired

  })).isRequired,
}

export default FriendsList;